<script>
    export let floatVideo;

    let videoContainer;
    let smallVideo;

    const removeFloatVideo = () => {
        videoContainer.classList.remove('float');
        smallVideo.classList.remove('sm-player');
    }
</script>

<!-- MarkUp -->
<section>
	<div id="video-cont" class="video-cont" class:float={floatVideo} bind:this={videoContainer}>
		{#if floatVideo}
			<div id="close-icon" on:click={removeFloatVideo}>X</div>
		{/if}
		
		<video class="vid-player" controls class:sm-player={floatVideo} bind:this={smallVideo}>	
			<track kind="captions" />
			<source src="https://media.istockphoto.com/videos/time-lapse-sunrise-over-mountain-with-mist-and-fog-cloud-video-id1125217870" type="video/webm">
			<source src="https://media.istockphoto.com/videos/time-lapse-sunrise-over-mountain-with-mist-and-fog-cloud-video-id1125217870" type="video/mp4"> 
				Sorry, your browser doesn't support embedded videos.
		</video>	
	</div>
</section>

<style>
	section {
		min-height: 30rem;
	}
    .video-cont {
		max-width: 70%;
        margin: 0 auto 2rem;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		z-index: 10;
	}

	.vid-player {
		width: 100%;
	}

	#close-icon {
		display: block;
		position: absolute;
		font-size: 1rem;
		color: white;
		font-weight: 800;
		top: 1%;
		right: 3%;
		cursor: pointer;
	}

	#close-icon:active {
		color: red;
	}

    /* small video player when scrolling */
	@keyframes fade {
		100% { opacity: 1 }
	}

	@keyframes enterR {
		0% { right: -40% }
	}

	.sm-player {
		width: 270px;
		height: 160px;
	}

    .float {
		animation: enterR .5s ease-in-out;
		width: 290px;
		height: 200px;
		box-shadow: 0 0 7px darkgray;
		background-color: #000;
		position: fixed;
		top: 1%;
		right: 1%;
	}
</style>